import { createStore } from 'vuex'


//把购物车里的信息放入本地存储-持久存储
const setLocalCartList =(state)=>{
  const { cartList } = state;
  // const cartListString = cartList.toJON()

  //转换为Json字符串模式(如果往localStorage存数据，必须是JSON.stringify字符串模式)
  const cartListString = JSON.stringify(cartList)

  localStorage.cartList = cartListString

  // console.log(localStorage.cartList);
}

//获取存储的本地信息信息
const getLocaCartList=()=>{
  //接受服务器数据一般是字符串，用JSON.parse的方法转换为javascript对象
 
  try{
    //接受json.parse 最好用try,catch包裹起来
    return JSON.parse(localStorage.cartList)
  }catch(e){
    return {}
  }
}


export default createStore({
  state: {

     cartList: getLocaCartList()


    //{shopId: { shopName:'', productList:{ productId:{} } } }
    // cartList:{
    //   //第一层级是商铺的ID
    //   // shopId:{

    //       //第二层商铺名称
    //       //shopName:''
          
    //       //第二层商铺情报
    //       //productList:{

    //         //第三层商品ID
    //         //productId:{

    //             //第三层的商品情报
    //             //_id: '1',
    //             //name: '番茄250g/份',
    //             //imgUrl: 'http://www.dell-lee.com/imgs/vue3/tomato.png',
    //             //sales: 10,
    //             //price: 33.6,
    //             //oldPrice: 39.6,
    //             //count:2
    //         //},
    //   //},
    // }
  },
  mutations: {
    //存储放入购物车理的数据
    //接受传入的数据
    //state 的数据
    //payload 传递进来的数据
    //店铺ID，商品ID, 商品信息
    //cartList?.[shopId]?.[item._id]?.count
    changeCartItemInfo(state,payload){
      const { shopId, productId, productInfo } = payload
      
      //vue会把state里的数据弄成响应式数据
      //获取state
      // let shopInfo = state.cartList[shopId]
      // if( !shopInfo){shopInfo = {} }
      // 上面两句的结合(因为是两层对象，需要些内容，不能写空)
      let shopInfo = state.cartList[shopId] || { shopName:'', productList:{} }

      //第二层内容,商品ID
      let product = shopInfo.productList[productId]

      //如果不存在获取点击的内容
      if(!product){ 
        productInfo.count = 0
        product = productInfo 

        // product.count = 0

        //判断放入购物车里的对号选中
        // product.check = 0
      }

      //如果存在product 增加或减少 payload.num控制增加减少
      product.count = product.count + payload.num

      //如果假如商品就让check变成true , 判断购物车的选中情况
      if(payload.num > 0){ product.check = true }
      //只有前面是真，才会执行后面
      // (payload.num > 0) && (product.check = true )

      //数量不少于0
      if(product.count < 0){ product.count = 0 }
      //只有前面是真，才会执行后面
      // (product.count < 0) && (product.count = 0 )

      //同时从新存入shopInfo
      shopInfo.productList[productId] = product

      //处理结束之后的数据，从新存入state里
      state.cartList[shopId] = shopInfo

      // console.log(state.cartList);

      setLocalCartList(state)
    },

    //修改商铺名称
    changeShopName(state, payload){
      const { shopId, shopName} = payload
      
      //获取state > cartList > shopId 的情报
      //如果改变店铺名称，没有数据
      //就赋予一个默认的信息
      const shopInfo = state.cartList[shopId] || { shopName:'', productList:{} }

      //赋予传递过来的店铺名称
      shopInfo.shopName = shopName

      //在赋予给对应的店铺
      state.cartList[shopId] = shopInfo

      setLocalCartList(state)

    },

    //鼠标点击，改变对号符号状态
    changeCartItemChecked(state, payload){
      //解构payload
      const { shopId, productId} = payload

      //获取当前商品情报
      const product = state.cartList[shopId].productList[productId]

      //点击取反
      product.check = !product.check

      setLocalCartList(state)
    },

    //清空购物车
    cleanCartProducts(state, payload){
      const { shopId } = payload

      //对应的购物车的shopId的对象为空
      state.cartList[shopId].productList = {}

      setLocalCartList(state)
    },

    //选中所有商品
    setCartItemsChecked(state, payload){

      const { shopId } = payload

      //获取当前店铺里的信息
      const products = state.cartList[shopId].productList

      if(products){
        for( let key in products){
          //获取当前店铺所有商品情报
          const product = products[key]
          //让所有商品都被选中
          product.check = true
        }
      }
      setLocalCartList(state)
    },
    clearCartData(state, shopId){
      console.log('执行');
      //清空购物车
      state.cartList[shopId].productList = {}
    }
  }
  
  // actions: {
  // },
  // modules: {
  // }
})
