import { createRouter, createWebHashHistory } from 'vue-router'
//只访问当前页时需要写
// import Home from '../views/home/Home'
// import Shop from '../views/shop/Shop'
// import Register from '../views/register/Register'
// import Login from '../views/login/Login'

const routes = [
  {
    path: '/',
    name: 'Home',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "home" */ '../views/home/Home')
  },{
    path: '/orderList',
    name: 'OrderList',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "oderConfirmation" */ '../views/orderList/OrderList')
  },{
    path: '/oderConfirmation/:id',
    name: 'OderConfirmation',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "oderConfirmation" */ '../views/oderConfirmation/OderConfirmation')
  },{
    path: '/cartList',
    name: 'CartList',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "cartList" */ '../views/cartList/CartList')
  },{
    path: '/shop/:id',
    name: 'Shop',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "shop" */ '../views/shop/Shop')
  },{
    path: '/register',
    name: 'Register',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "shop" */ '../views/register/Register'),
    //只有访问Login页面之前进行逻辑
    beforeEnter(to, from, next){
      //解构赋值
      const { isLogin } = localStorage;
      //要跳转到的页面
      isLogin ? next({name:'Home'}):next();
    }
  },{
    path: '/login',
    name: 'Login',
    //动态组件(只有在访问当页的时候才会加载)
    component: () => import(/* webpackChunkName: "shop" */ '../views/login/Login'),
    //只有访问Login页面之前进行逻辑
    beforeEnter(to, from, next){
      //解构赋值
      const { isLogin } = localStorage;
      isLogin ? next({name:'Home'}):next();
    }
  }

  
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

//不管什么路由都进行切换判断
router.beforeEach((to,from,next)=>{
  //普通赋值
  // const isLogin = localStorage.isLogin;
  //解构赋值
  const { isLogin } = localStorage;
  const { name } = to;
  const isLoginOrRegister = ( name === "Login" || name === "Register" );
  // if(isLogin || to.name === "Login"){
  //   next();
  // }else{
  //   next({name:'Login'});
  // }
 (isLogin || isLoginOrRegister ) ? next():next({name:'Login'});
})


export default router
